import './Footer.css'
import { useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { HashLink } from 'react-router-hash-link/dist/react-router-hash-link.cjs.production';
function Footer() {
    const { pathname } = useLocation();
    useEffect(() => {
        window.scrollTo(0, 0);
      }, [pathname]);
    return (  
        <div className="main__footer">
            <div className="footer">
                <div className="footer__column">
                    <div className="footer__img"><img src="./img/LogoLight.svg" alt="" /></div>
                    <p className='agree'>
                         Visual Space 2023.  ,     ,    <a className='agree' href="#" > </a>
                    </p>
                </div>
                <div className="footer__column">
                    <Link className='foot__head__link' to='/about'> </Link>
                    <Link className='foot__head__link' to='/works'> </Link>
                    <HashLink className='foot__link' to='/works#eks'></HashLink>
                    <HashLink className='foot__link' to='/works#int'></HashLink>
                    <HashLink className='foot__link' to='/works#3d'>3D </HashLink>
                    <HashLink className='foot__link' to='/works#web'>Web - </HashLink>
                </div>
                <div className="footer__column">
                    <h4 className='foot__head'></h4>
                    <Link className='foot__link' to='/'> </Link>
                    <Link className='foot__link' to='/intvisual'> </Link>
                    <Link className='foot__link' to='/modeling'>3D </Link>
                    <Link className='foot__link' to='/design'>Web - </Link>
                </div>
                <div className="footer__column">
                    <p className='foot__head'></p>
                    <span>+7 937 586 11 37</span>
                    <span>E - mail</span>
                    <div className="footer__icons">
                        <a href="#"><img src="../img/LTelegram.png" alt="Telegram" /></a>
                        <a href="#"><img src="../img/LWhatsapp.png" alt="Whatsapp" /></a>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Footer;